import Link from "next/link";
import Image from "next/image";
import { useState } from "react";

export default function EventsPage() {
  const [search, setSearch] = useState("");

  const events = [
    {
      id: 1,
      title: "Los Angeles Lakers vs Miami Heat",
      date: "Jan 18, 2025",
      location: "Crypto Arena, LA",
      img: "/images/sports.jpg",
      category: "Sports",
    },
    {
      id: 2,
      title: "Arijit Singh Live Concert",
      date: "Feb 12, 2025",
      location: "Mumbai",
      img: "/images/concerts.jpg",
      category: "Concert",
    },
    {
      id: 3,
      title: "Broadway: The Lion King",
      date: "Feb 25, 2025",
      location: "New York Theatre",
      img: "/images/theatre.jpg",
      category: "Theatre",
    },
    {
      id: 4,
      title: "Sunburn Festival",
      date: "Dec 27, 2025",
      location: "Goa",
      img: "/images/festival.jpg",
      category: "Festival",
    },
  ];

  const filteredEvents = events.filter((e) =>
    e.title.toLowerCase().includes(search.toLowerCase())
  );

  return (
    <div className="min-h-screen bg-gray-900 text-white px-6 md:px-16 py-16">
      <h1 className="text-4xl font-bold mb-6">Upcoming Events</h1>

      {/* SEARCH BAR */}
      <div className="max-w-xl mb-12">
        <input
          type="text"
          placeholder="Search events..."
          value={search}
          onChange={(e) => setSearch(e.target.value)}
          className="w-full px-5 py-3 rounded-xl bg-gray-800 border border-gray-700 text-white focus:ring-2 focus:ring-blue-500 outline-none"
        />
      </div>

      {/* EVENTS GRID */}
      <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-10">
        {filteredEvents.map((event) => (
          <div
            key={event.id}
            className="bg-gray-800 rounded-xl shadow-lg hover:shadow-blue-500/20 hover:scale-[1.02] transition p-4"
          >
            <Image
              src={event.img}
              width={500}
              height={300}
              alt={event.title}
              className="rounded-lg mb-4"
            />

            <div className="text-sm text-blue-400 font-semibold">
              {event.category}
            </div>

            <h2 className="text-xl font-bold mt-2">{event.title}</h2>

            <p className="text-gray-400 mt-1">{event.location}</p>

            <p className="text-gray-400 mt-1">{event.date}</p>

           <Link href={`/events/${event.id}`}>
  <button className="mt-4 w-full py-2 bg-blue-600 hover:bg-blue-500 rounded-lg">
    View Details
  </button>
</Link>
          </div>
        ))}
      </div>

      {filteredEvents.length === 0 && (
        <p className="text-gray-400 mt-8 text-lg">No events found.</p>
      )}
    </div>
  );
}
